@import "./colors.scss";
@import "./fonts.scss";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: $BACKGROUND;
}

// Content

.row {
  margin: 0 10% 0 10%;
  display: flex;
  color: $SECONDARY_COLOR;
  text-align: justify;
}

.column {
  padding: 1%;
  flex: 50%;
  justify-content: center;
  align-items: center;
}

.content-headline {
  font-size: 80px;
  font-weight: 400;
}

.content-wrapper {
  position: relative;
  padding-bottom: 56.25%;
}

.tedx-iframe {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

// contents from app.scss
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
//Global Styles

.flex-container {
  margin: 0 10% 0 10%;
  display: flex;
}

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

// temp footer styling
.footerClass {
  color: white;
}
.page-wrapper {
  padding-top: 90px;
  min-height: 100vh;
}
